<template>
    <WikiPanel
        class="m-relations-panel"
        scene="detail"
        v-if="(relations && relations.length) || npc"
    >
        <template slot="head-title">
            <i class="el-icon-link"></i>
            <span></span>
            <em class="u-remark">BOSS</em>
        </template>
        <template slot="head-actions">
            <el-button
                type="primary"
                class="u-boss"
                :class="{ on: show_npc }"
                @click="show_npc = !show_npc"
                >BOSS
            </el-button>
        </template>
        <template slot="body">
            <div class="m-section">
                <div class="u-empty" v-if="!relations || !relations.length">
                    <span v-if="relations === null"> ...</span>
                    <span v-if="relations === false"> </span>
                    <span v-if="relations && !relations.length"
                        > </span
                    >
                </div>
                <div class="m-relations" v-if="relations && relations.length">
                    <router-link
                        class="u-title"
                        v-for="(relation, key) in relations"
                        :key="key"
                        :to="{
                            name: 'view',
                            params: { source_id: relation.ID },
                        }"
                    >
                        <img
                            class="u-icon"
                            :src="icon_url(relation.IconID)"
                            @error.once="
                                () => {
                                    $event.target.src = icon_url();
                                }
                            "
                        />
                        <span class="u-text" v-text="relation.Name"></span>
                    </router-link>
                </div>
            </div>
            <div class="m-section m-bossinfos" v-show="show_npc" v-if="npc">
                <ul class="m-bossinfo">
                    <li class="u-id">ID:<em></em>{{ npc.ID }}</li>
                    <li class="u-name">:<em></em>{{ npc.Name }}</li>
                    <li class="u-desc"><em>Title</em>{{ npc.Title }}</li>
                    <li class="u-desc"><em>Level:</em>{{ npc.Level }}</li>
                    <li class="u-desc">
                        <em>Intensity</em>{{ npc.Intensity }}
                    </li>
                    <li class="u-desc">
                        <em>MapName</em>{{ npc.MapName }}
                    </li>
                    <li class="u-desc">
                        <em>_Notation</em>{{ npc._Notation }}
                    </li>

                    <li class="u-desc">
                        <em>MaxLife</em>{{ npc.MaxLife }}
                    </li>
                    <li class="u-desc">
                        <em>MaxMana</em>{{ npc.MaxMana }}
                    </li>
                    <li class="u-desc">
                        <em>RunSpeed</em>{{ npc.RunSpeed }}
                    </li>
                    <li class="u-desc">
                        <em>WalkSpeed</em>{{ npc.WalkSpeed }}
                    </li>
                    <li class="u-desc" title="/64">
                        <em>TouchRange</em>{{ npc.TouchRange }}
                    </li>
                </ul>

                <ul class="m-bossinfo">
                    <li class="u-desc"><em>Sense</em>{{ npc.Sense }}</li>
                    <li class="u-desc"><em>Dodge</em>{{ npc.Dodge }}</li>
                    <li class="u-desc">
                        <em>PhysicsShieldBase</em
                        >{{ npc.PhysicsShieldBase }}
                    </li>
                    <li class="u-desc">
                        <em>NeutralMagicDefence</em
                        >{{ npc.NeutralMagicDefence }}
                    </li>
                    <li class="u-desc">
                        <em>SolarMagicDefence</em
                        >{{ npc.SolarMagicDefence }}
                    </li>
                    <li class="u-desc">
                        <em>LunarMagicDefence</em
                        >{{ npc.LunarMagicDefence }}
                    </li>
                    <li class="u-desc">
                        <em>PoisonMagicDefence</em
                        >{{ npc.PoisonMagicDefence }}
                    </li>
                </ul>

                <ul class="m-bossinfo">
                    <li class="u-desc">
                        <em>PhysicsCriticalStrike</em
                        >{{ npc.PhysicsCriticalStrike }}
                    </li>
                    <li class="u-desc">
                        <em>NeutralCriticalStrike</em
                        >{{ npc.NeutralCriticalStrike }}
                    </li>
                    <li class="u-desc">
                        <em>SolarCriticalStrike</em
                        >{{ npc.SolarCriticalStrike }}
                    </li>
                    <li class="u-desc">
                        <em>LunarCriticalStrike</em
                        >{{ npc.LunarCriticalStrike }}
                    </li>
                    <li class="u-desc">
                        <em>PoisonCriticalStrike</em
                        >{{ npc.PoisonCriticalStrike }}
                    </li>
                </ul>

                <ul class="m-bossinfo">
                    <li class="u-desc">
                        <em>PhysicsAttackHit</em
                        >{{ npc.PhysicsAttackHit }}
                    </li>
                    <li class="u-desc">
                        <em>NeutralMagicHit</em
                        >{{ npc.NeutralMagicHit }}
                    </li>
                    <li class="u-desc">
                        <em>SolarMagicHit</em>{{ npc.SolarMagicHit }}
                    </li>
                    <li class="u-desc">
                        <em>LunarMagicHit</em>{{ npc.LunarMagicHit }}
                    </li>
                    <li class="u-desc">
                        <em>PoisonMagicHit</em>{{ npc.PoisonMagicHit }}
                    </li>
                </ul>

                <ul class="m-bossinfo">
                    <li class="u-desc">
                        <em>CanSeeLifeBar</em
                        >{{ npc.CanSeeLifeBar }}
                    </li>
                    <li class="u-desc">
                        <em>IsSelectable</em>{{ npc.IsSelectable }}
                    </li>

                    <li class="u-desc">
                        <em>ReviveTime</em>{{ npc.ReviveTime }}
                    </li>
                </ul>
            </div>
        </template>
    </WikiPanel>
</template>

<script>
const { JX3BOX } = require("@jx3box/jx3box-common");
import WikiPanel from "@jx3box/jx3box-common-ui/src/WikiPanel";
import { get_relation_achievements } from "@/service/achievement";
import { iconLink } from "@jx3box/jx3box-common/js/utils";

export default {
    name: "Relation",
    props: ["sourceId"],
    data() {
        return {
            relations: null,
            npc: null,
            show_npc: false,
        };
    },
    methods: {
        icon_url: iconLink,
        // boss
        getBossInfo(npcid) {
            npcid &&
                this.$http
                    .get(`${JX3BOX.__node}npc/id/${npcid}`)
                    .then((res) => {
                        res = res.data;
                        if (res.list && res.list.length) this.npc = res.list[0];
                    });
        },
    },
    components: {
        WikiPanel,
    },
    watch: {
        sourceId: {
            immediate: true,
            handler() {
                if (this.sourceId) {
                    get_relation_achievements(this.sourceId).then((res) => {
                        res = res.data;
                        if (res.code === 200) {
                            let result = res.data;
                            this.relations = result.relations;

                            // boss
                            this.getBossInfo(result.boss_id);
                        }
                    });
                }
            },
        },
    },
};
</script>

<style lang="less">
@import "../../assets/css/components/achievement/relations.less";
</style>
